<template>
  <div>
    <a :name="anchor"></a>
    <div class="h-show">
      <label>{{name}}</label>
      <div>
        <slot></slot>
      </div>
      <hr style="height:1px;border:none;border-top:2px dashed #e0e0e0;">
      <div>
        <el-row :gutter="20" style="margin:10px 0">
          <el-col :span=12 style="padding:0">
            <pre>
              <code>
                {{code}}
              </code>
            </pre>
          </el-col>
          <el-col :span=12>
            {{description}}
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    components: {
    },
    props:{
      name:String,
      description:String,
      code:String,
      anchor:String
    },
    data() {
      return {
      };
    },
    methods:{
    }
  }
</script>
<style scoped lang="scss">
.h-show{
  background-color: #fff;
  border: 1px solid #e0e0e0;
  max-width: 970px;
  margin: 0 auto;
  padding: 15px;
  min-height: 100px;
  margin-bottom: 20px;
  label{
    display: block;
    padding-bottom: 10px;
  }
  pre{
    background-color: #eaeefb;
    font-family: Menlo,Monaco,Consolas,Courier,monospace;
    white-space: pre;
    margin: 0;
  }
}
</style>


