<template>
<div class="contain">
  <div class="left">
    <aside>
      <div>
        vue 组件
      </div>
      <ul>
        <li v-for="(item,index) in aside" :key="index">
          <a :href="item.href">{{item.name}}</a>
        </li>
      </ul>
    </aside>
  </div>
  <div class="right">

    <div class="choose_theme">
      <div @click="changeTheme(item.theme)" :style="{'background-color':item.color}" v-for="(item,index) in themeColor" :key="index"></div>
    </div>



    <codebox 
      name="导航栏"
      description="这是一个导航栏"
      anchor="h-navbar"
      :code="codeNavBar">
      <navBar :menus="menus"></navBar>
    </codebox>
    <codebox 
      name="侧栏导航"
      description="侧栏导航"
      anchor="h-menu"
      :code="codeNavBar">
      <div style="width:200px">
        <hMenu :menus="menus"></hMenu>
      </div>
    </codebox>
    <codebox
      name="树组件"
      description="这是一个树组件"
      anchor="h-tree"
      :code="codeNavBar">
      <tree-t :onChoose="onChoose" :model="treeData"/>
    </codebox>
    <codebox
      name="布局"
      description="使用flex布局，实现自适应布局界面"
      anchor="h-layout">
      <div style="min-height:150px">
        <layout>
          <hMenu :menus="menus" slot="aside"></hMenu>
          <!-- <div slot="aside">菜单</div> -->
          <div slot="main">main</div>
        </layout>
      </div>
    </codebox>
  </div>
</div>
 
</template>

<script>
import navBar from '../../components/h-navbar/h-navbar.vue'
import hMenu from '../../components/h-menu/h-menu.vue'
import layout from '../../components/h-layout/h-layout.vue'
import codebox from '../../components/h-codebox/h-codebox.vue'

import MockData from './mockdata.js'
export default {
  components: {
    navBar,
    layout,
    codebox,
    hMenu
  },
  data () {
    return {
      menus: MockData.menus,
      treeData: MockData.treeData,
      codeNavBar:'<navBar :menus="menus"></navBar>',
      aside:[{
        href:'#h-navbar',
        name:'导航栏'
      },{
        href:'#h-menu',
        name:'侧栏导航'
      },{
        href:'#h-tree',
        name:'树组件'
      },{
        href:'#h-layout',
        name:'布局'
      }],
      themeColor:[{
        theme:'theme_blue',
        color:'rgba(64, 158, 255, .9)'
      },{
        theme:'theme_green',
        color:'#00d1b2'
      },{
        theme:'theme_pink',
        color:'#f39894'
      },{
        theme:'theme_orange',
        color:'#f4b976'
      },{
        theme:'theme_cyan',
        color:'#26b6be'
      },{
        theme: 'theme_black',
        color:'#545c64'
      }]
    }
  },
  methods:{
    onChoose(t){
      console.log(t)
    },
    changeTheme (theme) {
      window.document.documentElement.setAttribute('data-theme', theme)
    }
  }
}
</script>


<style scoped lang="scss">
.contain{
  display: flex;
  flex-direction: row;
  flex: auto;
  min-height: 1500px;
  .left{
    flex: 0 0 100px;
    aside{
      position: fixed;
      top: 0;
      margin-left: 50px;
      div{
        background-color: #000;
        color: #fff;
        padding: 20px;
      }
      ul{
        padding-left: 20px;
      }
    }
  }
  .right{
    flex: auto;
    .choose_theme{
      text-align: center;
      div{
        display: inline-block;
        width: 100px;
        height: 60px;
        margin: 0 5px;
        cursor: pointer;
      }
    }
  }
}

</style>


