<template>
  <ul class="h-menu">
    <li v-for="(item,index) in menus" @click="showSubMenu(item,index)" :key="index">
      <div class="h-menu-title" :class="{'h-menu-title-active':activeIndex==index}">
        <i class="iconfont icon-angular"></i>
        <span>{{item.name}}</span>
        <i class="iconfont" :class="{true:'icon-shang',false:'icon-xia'}[item.collapsed||!!item.collapsed]"></i>
      </div>
      <transition name="el-zoom-in-top">
        <ul v-show="item.children&&item.collapsed">
          <li v-for="(subItem,index) in item.children" :key="index">
            <div>{{subItem.name}}</div>
          </li>
        </ul>
      </transition>
    </li>
  </ul>
</template>

<script>
  export default {
    
    props:{
      menus:{
        type: Array,
        default: []
      },
      collapsed:{
        type:Boolean,
        default: false
      }
    },
    data() {
      return {
        activeIndex: 0
      };
    },
    methods:{
      showSubMenu(item,index){
        this.activeIndex = index
        if(item.hasOwnProperty('collapsed')){
          item.collapsed = !item.collapsed
        }else{
          this.$set(item,'collapsed',true)
        }
      }
    }
  }
</script>
<style scoped lang="scss">
@import '../../themes/mixin.scss';
.h-menu{
  width: 100%;
  padding: 0;
  list-style: none;
  font-size: 14px;
  margin: 0;
  box-sizing: border-box;
  @include bg_color($background-color-theme);
  color: #fff;
  &-title{
    position: relative;
    transition: background .3s,width .2s;
    :nth-child(1){
      left: 40px;
      position: absolute;
    }
    :nth-child(3){
      right: 20px;
      font-size: 12px;
      opacity: .7;
      position: absolute;
    }
    &-active{
      background-color: indianred;
    }
  }
  ul{
    list-style: none;
    background-color: rgba(0,0,0,.4);
    margin: 0;
    padding: 0;
  }
  li{
    cursor: pointer;
    text-align: center;
    div{
      height: 40px;
      line-height: 40px;
    }
    div:hover{
      background-color: rgba(125,125,125,.8);
    }
  }
}
</style>


