<template>
  <header class="h-nav-bar">
    <div class="header">
      <div class="logo">Vue组件</div>
      <nav>
        <ul>
          <li v-for="(item,index) in menus" :key="index" :id="index" @click="clickMenus(index)" @mouseover="onMouseOver($event,index)">
            <a>
              {{item.name}}
              <sub :class="{'active':activeIndex==index}">&nbsp;</sub>
            </a>
          </li>
        </ul>
      </nav>
    </div>
    <transition name="el-zoom-in-top">
      <div style="position:relative">
        <nav class="subnav" v-show="showSubNav" @mouseleave="showSubNav=false">
          <div v-for="(item,index) in subMenus" :key="index">
            <router-link :to="item.to">
              {{item.name}}
            </router-link>
            <label v-if="index!=subMenus.length-1">|</label>
          </div>
        </nav>
      </div>
    </transition>
  </header>
</template>
<script>
  export default {
    props:{
      logo:[],
      menus:{
        type: Array,
        default: []
      }
    },
    data() {
      return {
        showSubNav: false,
        subMenus:[],
        activeIndex: 0
      };
    },
    methods:{
      onMouseOver(event,index){
        this.showSubNav = true&&this.menus[index].hasOwnProperty('children');
        this.subMenus = this.menus[index].children||[];
      },
      clickMenus(index){
        this.activeIndex = index;
      }
    }
  }
</script>
<style scoped lang="scss">
@import '../../themes/mixin.scss';
.h-nav-bar{
  @include bg_color($background-color-theme);
  .header{
    max-width: 960px;
    margin: auto;
  }
  .header:after{
    display: table;
    line-height: 0;
    content: "";
    clear: both;
  }
  .logo{
    min-width:30%;
    float: left;
    line-height: 50px;
    text-align: center;
    color: #fff;
  }
  .subnav{
    min-height: 50px;
    background-color: #fff;
    width: 100%;
    position: absolute;
    left: 0;
    z-index: 99;
    box-shadow: 0 0 1px 1px #e0e0e0;
    text-align: center;
    a{
      color: #000;
      display: inline-block;
      padding: 0 20px;
    }
    a:hover{
      font-weight: bold;
    }
    div{
      display: inline-block;
      font-size: 14px;
    }
  }
  nav{
    float: left;
    @include px2rem(line-height,50);
  }
  nav:after{
    display: table;
    line-height: 0;
    content: "";
    clear: both;
  }
  ul,li{
    margin: 0;
    padding: 0;
    height:50px;
    list-style: none;
    float: left;
    position: relative;
  }
  ul li a{
    color: #fff;
    padding: 0 16px;
    display: block;
    font-size: 15px;
    cursor: pointer;
  }
  ul li sub{
    margin: auto;
    display: inline-block;
    position: absolute;
    bottom:0;
    left: 0;
    right: 0;
    width: 0;
    height:3px;
    transition: .5s;
  }
  ul li:hover{
    background-color: rgba(0,0,0,.3);
    sub{
      width: 100%;
      left: 0;
      background: red;
    }
  }
  sub.active{
    width: 100%;
    left: 0;
    background: red;
  }
}
</style>


