<template>
  <div class="h-layout">
    <aside :class="{'layout-aside-collapsed':collapsed}">
      <slot name="aside"></slot>
    </aside>
    <main>
      <nav>
        <i @click="collapsed=!collapsed" class="iconfont" :class="{'icon-weibiaoti26':collapsed,'icon-weibiaoti25':!collapsed}"></i>
      </nav>
      <div>
        <slot name="main"></slot>
      </div>
    </main>
  </div>
</template>
<script>
  export default {
    components: {
    },
    props:{
    },
    data() {
      return {
        collapsed: false
      };
    },
    methods:{
    }
  }
</script>
<style scoped lang="scss">
@import '../../themes/mixin.scss';
.h-layout{
  display: flex;
  flex-direction: row;
  flex: auto;
  height: 100%;
  .layout-aside-collapsed{
    flex: 0 0 80px;
    max-width: 80px;
    min-width: 80px;
  }
  main{
    display: flex;
    flex: auto;
    flex-direction: column;
    background-color: #e0e0e0;
    nav{
      height: $main-nav-height;
      line-height: $main-nav-height;
      flex: 0 0 auto;
      background-color: #fff;
      i{
        line-height: $main-nav-height;
        margin-left: .5rem;
        font-size: 20px;
        cursor: pointer;
      }
      i:hover{
        color: $main-nav-hover;
      }
    }
  }
  aside{
    flex: 0 0 200px;
    max-width: 200px;
    min-width: 200px;
    width: 200px;
    background-color: rgba(0,0,0,.8);
    transition: all .2s;
  }

}
</style>


