import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Index'
import indexEdit from '@/views/edit/Index'
import Edit from '@/views/edit/Edit'
import Chart from '@/views/edit/Chart.vue'
import Lab from '@/views/edit/Lab'

import Read from '@/views/read/Read'
import Read2 from '@/views/read/ReadTwo'
import Koa from '@/views/koa/Koa'
import Tree from '@/views/tree/Tree'
import Test from '@/views/test/test'

Vue.use(Router)

const router =  new Router({
    routes: [{
        path: '/',
        redirect: '/test'
    },{
        path:'/test',
        name:'Test',
        component:Test
    }, {
        path: '/home',
        name: 'Home',
        component: Home,
    },{
        path: '/edit',
        component: indexEdit,
        children:[{
            path: '/',
            component: Edit,
        },{
            path: 'lab/:id', //动态路由匹配 $route.params 获取当前对象{id:xxx}
            component:Lab
        },{
            path: 'chart',
            component:Chart
        }]
    }, {
        path: '/read',
        name: 'Read',
        component: Read
    },  {
        path: '/read2',
        name: 'Read2',
        component: Read2
    },{
        path: '/tree',
        name: 'Tree',
        component: Tree
    },{
        path: '/koa',
        name: 'Koa',
        component: Koa
    },{
        path:'*',
        component:  { 
            template: '<div><h3>404</h3><pre>404</pre></div>' 
        }
    }]
})
router.beforeEach((to,from,next)=>{
    console.log(`全局导航守卫:s%s%`,to,from)
    next()
})
/**
 * , {
        path: '/vuetool',
        name: 'VueTool',
        component: VueTool
    }, {
        path: '/app',
        name: 'App',
        component: App
    }, {
        path: '/koa',
        name: 'Koa',
        component: Koa
    }
 */

export default router


